.friendMessage {
    width: 575px;
    height: 77px;
    padding: 8px;
    margin: 24px 0 0 70px;

    font-family: 'Montserrat', sans-serif;

    background-color: #F5F7FB;
    box-shadow: 0px 1px 2px 0px #1D21261A, 0px 5px 20px 0px #1D212608;
}

.friendImageAndText {
    display: flex;

    position: relative;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;

    /*position: absolute;*/
}

.friendText {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    margin-left: 25px;
}

.friendText:after {

}

.friendName {
    font-size: 18px;
    font-weight: 600;

    margin-bottom: 3px;
}

.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    color: #000000;

    margin: 5px 0 0 11px;
}
