.sendForm {
    margin:50px 0 32px;

    display: flex;
    gap: 44px;

}

.textarea {
    box-sizing: border-box;
    width: 976px;
    border-radius: 20px;
    padding: 12px 24px;
    margin-left: 70px;

    background-color: #F5F7FB;
    border: none;
    box-shadow: 0px 1px 2px 0px #1D21261A, 0px 5px 20px 0px #1D212608;
    
    resize: none;
    overflow: hidden;


    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 400;
}

.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;

    background-color: #0066CC;
    border: none;

    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 600;
    color: white;
}

