.message {
    background-color: #0066CC;
    box-shadow: 0px 1px 2px 0px #1D21261A, 0px 5px 20px 0px #1D212608;

    width: 575px;
    height: 77px;
    padding: 8px;
    margin: 24px 70px 0 0;

    align-self: flex-end;

    font-family: 'Montserrat', sans-serif;
    color: #FFFFFF;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;

    position: relative;

}

.imageAndText img {
    /*position: absolute;*/
    width: 48px;
    height: 48px;
}

.text {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    margin-right: 25px;
}

.text:after {

}

.name {
    font-size: 18px;
    font-weight: 600;
    text-align: right;

    margin-bottom: 3px;
}

.messageText {
    font-family: 'Montserrat', sans-serif;

    font-size: 16px;
    text-align: right;
}

.time {
    font-size: 10px;
    font-weight: 600;
    color: #000000;

    text-align: right;
    margin: 5px 11px 0 0;
}