.greetingForm {
    font-family: Montserrat, sans-serif;

    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 30px 70px;
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    color: #CC0000;
    font-size: 14px;
    font-weight: 400;
}

input {
    width: 372px;
    height: 36px;
    border-radius: 5px;
    border: 1px solid #D1D1D1;

    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 500;
}

input:focus {}

.errorInput {
    border: 1px solid #CC0000;
}

.button {
    background-color: #0066CC;
    color: #FFFFFF;
    border: none;
    border-radius: 3px;

    width: 78px;
    height: 36px;

    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;

}

.button:focus {}

.button:active {}

.button:disabled {
    opacity: 0.5;
}

.text {
    font-size: 14px;
    font-weight: 400;
    color: #00000080;
}

.greeting {
    font-size: 14px;
    font-weight: 500;
}